body {
  background-color: rgb(48, 48, 48);
  padding: none;
}

.page-container {
  font-family: monospace;
  display: flex;
  flex-direction: column;
}

/* Tree */
.flow-container {
  flex: 1;
}

.central-node {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 50%;
  background-color: #001e3c;
  color: white;
  border-radius: 10px;
  border: 2px solid #0076ed;
  padding: 5px;
  z-index: 1;
}

.node-wrapper {
  border: 1px solid #0076ed;
  border-radius: 50%;
  background: #001e3c;
  width: 5px;
  height: 5px;
  z-index: 1;
}

.address-node {
  visibility: hidden;
  position: relative;
  top: 350%;
  left: -3500%;
}

.node-wrapper:hover {
  background-color: #0076ed;
  box-shadow: 2px 2px 10px #2592ff, -2px -2px 10px #2592ff,
    2px -2px 10px #2592ff, -2px 2px 10px #2592ff;
}

.node-wrapper:hover .address-node {
  visibility: initial;
  color: #0076ed;
}

.load-circle {
  margin: auto;
  margin-top: 100px;
}

.error-text,
.flavour-text {
  margin: auto;
  margin-top: 100px;
  color: white;
  background-color: rgb(71, 71, 71);
  border-radius: 10px;
  padding: 1rem;
  border: 1px solid #0076ed;
}

/* Input */

.input-wrapper {
  font-family: monospace;
  border-radius: 10px;
  border: 1px solid #0076ed;
  display: flex;
  flex-direction: column;
  width: max-content;
  gap: 0.5rem;
  margin: 1rem;
  padding: 1rem;
  background-color: rgb(71, 71, 71);
}

.input-wrapper input {
  all: unset;
  background-color: #0076ed;
  padding: 0.5rem;
  border-radius: 25px;
  color: white;
  text-align: center;
}

input::placeholder {
  color: white;
  text-align: center;
}

.input-wrapper button {
  all: unset;
  background-color: rgb(48, 48, 48);
  padding: 0.5rem;
  border-radius: 25px;
  border: 1px solid #0076ed;
  color: white;
  text-align: center;
}

.logo-wrapper {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.icon-wrapper {
  width: 1.5rem;
  height: 1.5rem;
  color: white;
}

.logo {
  color: white;
}
